import { React, useEffect, useState } from "react";
import { withRouter } from "react-router";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Container,
  Button,
  Nav,
  NavDropdown,
  Form,
  FormControl,
  Row,
  Col,
  Card,
  CardGroup,
} from "react-bootstrap";
import { Navbar, Figure } from "react-bootstrap";
import "./App.css";
import WebFont from "webfontloader";
import banner from "./images/showcase.png";
import Radium, { StyleRoot } from "radium";
import "./fonts/Inter-Regular.ttf";
import "./fonts/ThirstyRoughBlack.otf";
import "./fonts/Montserrat-SemiBold.ttf";
import "./fonts/Montserrat-Black.ttf";
import { ethers } from "ethers";
import ApeContractData from "./blockchain/Contract";

const Head = (props) => {
  const styleheading = {
    // Adding media query..
    "@media (max-width: 800px)": {
      fontSize: "2em",
      textAlign: "center",
    },

    "@media (max-width: 600px)": {
      fontSize: "1em",
      textAlign: "center",
    },
  };

  const stylepara = {
    // Adding media query..
    "@media (max-width: 800px)": {
      fontSize: "0.7em",
      textAlign: "center",
    },

    "@media (max-width: 600px)": {
      fontSize: "0.7em",
      textAlign: "center",
    },
  };

  const stylebtn = {
    // Adding media query..
    "@media (max-width: 800px)": {
      width: "20%",
      height: "50%",
      fontSize: "10em",
    },

    "@media (max-width: 600px)": {
      width: "20%",
      height: "50%",
      fontSize: "0.5em",
    },
  };

  const mainimg = {
    // Adding media query..
    "@media (max-width: 2000px)": {
      backgroundImage: ` url(${banner}) `,
      backgroundSize: "cover",
      height: "35em",
    },

    "@media (max-width: 600px)": {
      backgroundImage: ` url(${banner}) `,
      backgroundSize: "cover",
      height: "100%",
    },
  };

  const fontset = {
    // Adding media query..
    "@media (max-width: 2000px)": {
      fontSize: "1.1em",
    },

    "@media (max-width: 800px)": {
      fontSize: "0.7em",
      margin: "0%",
    },
  };

  return (
    <>
      <StyleRoot>
        <div
          style={{
            backgroundImage: ` url(${banner}) `,
            backgroundSize: "cover",
            margin: "0%",
            height: "100%",
            paddingBottom: "200px",
            paddingTop: "200px",
          }}
        >
          <Container>
            <CardGroup style={{ width: "100%" }}>
              <Card
                style={{
                  backgroundColor: "transparent",
                  margin: "0%",
                  border: "none",
                  paddingBottom: "8%",
                  paddingTop: "8%",
                }}
              >
                {}
              </Card>

              <Card
                style={{
                  backgroundColor: "transparent",
                  margin: "0%",
                  border: "none",
                  paddingBottom: "8%",
                  paddingTop: "8%",
                  width: "100%",
                  height: "100%",
                }}
              >
                {}
              </Card>
              <Card
                style={{
                  backgroundColor: "transparent",
                  margin: "0%",
                  border: "none",
                }}
              ></Card>
            </CardGroup>
          </Container>
        </div>
      </StyleRoot>
    </>
  );
};

export default Head;
