import { React, useEffect } from "react";
import { withRouter } from "react-router";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Container,
  Button,
  Nav,
  NavDropdown,
  Form,
  FormControl,
  Row,
  Col,
  Card,
  CardGroup,
} from "react-bootstrap";
import { Navbar, Figure } from "react-bootstrap";
import "./App.css";
import WebFont from "webfontloader";
import Radium, { StyleRoot } from "radium";
import discord from "./images/discord.png";
import twitter from "./images/twitter.png";
import youtube from "./images/youtube.png";
import instagram from "./images/instagram.png";
import QRCode from "./images/QRCODE.png";
import "./fonts/ThirstyRoughBlack.otf";
import "./fonts/Montserrat-SemiBold.ttf";

const Partnership = (props) => {
  return (
    <>
      <StyleRoot id="Partnership">
        <div
          style={{
            backgroundColor: "black",
            backgroundSize: "cover",
            paddingBottom: "2%",
          }}
        >
          <h1
            style={{
              fontFamily: "ThirstyRoughBlack",
              color: "yellow",
              textAlign: "center",
              paddingTop: "2%",
            }}
          >
            Socials
          </h1> 
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "center",
              alignItems: "center",
            }}
            >
            <a href="https://discord.gg/Zr6zBK8SyA">
              <img
                src={discord}
                style={{ width: "100px", height: "100px" }}
                className="img-fluid"
              />
            </a>
            &nbsp;
            <a href="https://twitter.com/GameWorldsDev">
              <img src={twitter} style={{ width: "100px", height: "100px" }} className="img-fluid"/>
              </a>
            &nbsp;
            <a href="https://linktr.ee/GameWorlds">
              <img src={QRCode} style={{ width: "200px", height: "200px" }} className="img-fluid"/>
            </a>
            &nbsp;
            <a href="https://www.youtube.com/channel/UCAYoZDD6Mc7sT-YLDhdiHfw?sub_confirmation=1s">
              <img src={youtube} style={{ width: "100px", height: "100px" }} className="img-fluid" />
            </a>
            &nbsp;
            <a href="https://www.instagram.com/gameworldsdev/">
              <img
                src={instagram}
                style={{ width: "100px", height: "100px" }} className="img-fluid"
              />
            </a>
          </div>
        </div>
      </StyleRoot>
    </>
  );
};

export default Partnership;
