import { React, useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Container,
  Button,
  Nav,
  NavDropdown,
  Form,
  FormControl,
} from "react-bootstrap";
import { Navbar, Figure } from "react-bootstrap";
import "./App.css";
import WebFont from "webfontloader";
import logo from "./images/logo.gif";
import "./fonts/Inter-Regular.ttf";
import { ethers } from "ethers";
import { Link } from "react-scroll";

const Navibar = (props) => {
  const [text, setText] = useState("Connect Wallet");
  const getProvider = () => {
    return new ethers.providers.Web3Provider(window.ethereum);
  };

  const getSigner = () => {
    const provider = getProvider();
    return provider.getSigner();
  };

  // returns promise
  const getSignerAddress = () => {
    const provider = getProvider();
    return provider.getSigner().getAddress();
  };

  useEffect(() => {
    checkIfWalletIsConnected();
  }, []);

  function getMobileOperatingSystem() {
    var userAgent = navigator.userAgent || navigator.vendor || window.opera;

    if (
      userAgent.match(/iPad/i) ||
      userAgent.match(/iPhone/i) ||
      userAgent.match(/iPod/i)
    ) {
      return "iOS";
    } else if (userAgent.match(/Android/i)) {
      return "Android";
    } else {
      return "unknown";
    }
  }

  const checkIfWalletIsConnected = async () => {
    try {
      if (!window.ethereum) {
        if (
          getMobileOperatingSystem() === "iOS" ||
          getMobileOperatingSystem() == "Android"
        ) {
          window.location
            .replace
            // "https://metamask.io/download/"
            ();
        }
        return alert("Please install Metamask");
      } else {
        await window.ethereum.enable();
        const accounts = await window.ethereum.request({
          method: "eth_accounts",
        });
        console.log(accounts);
        const chainId = await window.ethereum.request({
          method: "eth_chainId",
        });
        console.log(chainId);
        if (chainId === "0x1") {
          if (accounts.length) {
            // Set Current Signer
            const signer = getSigner();

            // Set Current Signer Address
            const signerAddress = await getSignerAddress();
            setText("Connected");
          } else {
            console.log("No accounts found");
          }
        } else {
          alert("Please Select Ethereum Mainnet!");
        }
      }
    } catch (error) {
      alert(error.data.message);
      throw new Error("No window.ethereum Object");
    }
  };

  useEffect(() => {
    WebFont.load({
      google: {
        families: ["Thirsty Script", "Chilanka"],
      },
    });
  }, []);

  return (
    <>
      <Navbar
        style={{ backgroundColor: "#000", width: "100%", position: "fixed", zIndex:"99" }}
        expand="md"
      >
        <Container fluid row>
          <Navbar.Brand className="col-6" href="#">
            <img
              style={{
                width: "100px",
                height: "auto",
                padding: "0",
                margin: "0",
              }}
              src={logo}
              className="img-fluid"
              alt="Your Logo Here"
            />
          </Navbar.Brand>

          <Navbar.Toggle ranaria-controls="navbarScroll" style={{backgroundColor:"#FFCA2C"}} />
          <Navbar.Collapse
            className="row"
            id="navbarScroll"
            style={{ color: "white"}}
          >
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: "80px"}}
              navbarScroll
            ></Nav>
            <Nav
              className="align-items-center justify-content-end px-2"
              style={{ marginRight: "0%" }}
            >
              <a
                href="#Home"
                style={{
                  textDecoration: "none",
                  fontFamily: "Inter",
                  fontSize: "1.2em",
                  color: "white",
                  marginRight: "7%",
                  letterSpacing: "0.3em",
                  hover: "gray",
                }}
              >
                
              </a>

              <a
                href="#Mint"
                style={{
                  textDecoration: "none",
                  fontFamily: "Inter",
                  fontSize: "1.2em",
                  color: "white",
                  marginRight: "7%",
                  letterSpacing: "0.3em",
                  hover: "gray",
                }}
              >
                Mint
              </a>

              <a
                href="#About"
                style={{
                  textDecoration: "none",
                  fontFamily: "Inter",
                  fontSize: "1.2em",
                  color: "white",
                  marginRight: "7%",
                  letterSpacing: "0.3em",
                  hover: "gray",
                }}
              >
                About

                
              </a>

              <a
                href="#GameCoin"
                style={{
                  textDecoration: "none",
                  fontFamily: "Inter",
                  fontSize: "1.2em",
                  color: "white",
                  marginRight: "7%",
                  letterSpacing: "0.3em",
                  hover: "gray",
                }}
              >
                GameCoin
              </a>
              
              <a
                href="#Roadmap"
                style={{
                  textDecoration: "none",
                  fontFamily: "Inter",
                  fontSize: "1.2em",
                  color: "white",
                  marginRight: "7%",
                  letterSpacing: "0.3em",
                  hover: "gray",
                }}
              >
                Roadmap
              </a>
              
             

              <a
                href="#Team"
                style={{
                  textDecoration: "none",
                  fontFamily: "Inter",
                  fontSize: "1.2em",
                  color: "white",
                  marginRight: "7%",
                  letterSpacing: "0.3em",
                  hover: "gray",
                }}
              >
                Team
              </a>

              <a
                href="#Partnership"
                style={{
                  textDecoration: "none",
                  fontFamily: "Inter",
                  fontSize: "1.2em",
                  color: "white",
                  marginRight: "7%",
                  letterSpacing: "0.3em",
                  hover: "gray",
                }}
              >
                Partnership
              </a>

              <Button variant="warning">
                <span
                  style={{
                    fontFamily: "Montserrat-black",
                    paddingTop: "30%",
                    fontSize: "1.2em",
                  }}
                  onClick={checkIfWalletIsConnected}
                >
                  {text}
                </span>
              </Button>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
};

export default Navibar;
