import { React, useEffect, useState } from "react";
import { withRouter } from "react-router";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Container,
  Button,
  Nav,
  NavDropdown,
  Form,
  FormControl,
  Row,
  Col,
  Card,
  CardGroup,
} from "react-bootstrap";
import { Navbar, Figure } from "react-bootstrap";
import "./App.css";
import WebFont from "webfontloader";
import banner from "./images/showcase.png";
import Radium, { StyleRoot } from "radium";
import "./fonts/Inter-Regular.ttf";
import "./fonts/ThirstyRoughBlack.otf";
import "./fonts/Montserrat-SemiBold.ttf";
import "./fonts/Montserrat-Black.ttf";
import { ethers } from "ethers";
import ApeContractData from "./blockchain/Contract";
import masterpassg from "./images/masterpassg.gif";

const Mine = (props) => {
  const [mintValue, setMintValue] = useState(0);

  const incrementHandler = () => {
    if (mintValue >= 10) return;
    setMintValue(mintValue + 1);
  };

  const decrementHandler = () => {
    if (mintValue === 0) return;
    setMintValue(mintValue - 1);
  };

  const maxHandler = () => {
    setMintValue(10);
  };

  const getProvider = () => {
    return new ethers.providers.Web3Provider(window.ethereum);
  };

  const getSigner = () => {
    const provider = getProvider();
    return provider.getSigner();
  };

  // returns promise
  const getSignerAddress = () => {
    const provider = getProvider();
    return provider.getSigner().getAddress();
  };

  async function listenMMAccount() {
    window.ethereum.on("accountsChanged", async function () {
      window.location.reload();
    });

    window.ethereum.on("chainChanged", (currentChainId) => {
      window.location.reload();
    });
  }

  function getMobileOperatingSystem() {
    var userAgent = navigator.userAgent || navigator.vendor || window.opera;

    if (
      userAgent.match(/iPad/i) ||
      userAgent.match(/iPhone/i) ||
      userAgent.match(/iPod/i)
    ) {
      return "iOS";
    } else if (userAgent.match(/Android/i)) {
      return "Android";
    } else {
      return "unknown";
    }
  }

  const checkIfWalletIsConnected = async () => {
    try {
      if (!window.ethereum) {
        if (
          getMobileOperatingSystem() === "iOS" ||
          getMobileOperatingSystem() == "Android"
        ) {
          window.location
            .replace
            // "https://metamask.io/download/"
            ();
        }
        return alert("Please install Metamask");
      } else {
        await window.ethereum.enable();
        const accounts = await window.ethereum.request({
          method: "eth_accounts",
        });
        console.log(accounts);
        const chainId = await window.ethereum.request({
          method: "eth_chainId",
        });
        console.log(chainId);
        if (chainId === "0x1") {
          if (accounts.length) {
            // Set Current Signer
            const signer = getSigner();

            // Set Current Signer Address
            const signerAddress = await getSignerAddress();
          } else {
            console.log("No accounts found");
          }
        } else {
          alert("Please Select Ethereum Mainnet!");
        }
      }
    } catch (error) {
      alert(error.data.message);
      throw new Error("No window.ethereum Object");
    }
  };

  // Get APE Contract

  const getApeContract = async () => {
    const signer = getSigner();
    console.log(signer);
    if (!signer) {
      alert("Please Connect Wallet First!");
      // setIsLoading(false);
      return null;
    }

    const provider = getProvider();
    console.log(ApeContractData.address);
    console.log(provider);
    const ApeContract = new ethers.Contract(
      ApeContractData.address,
      ApeContractData.abi,
      provider
    );
    return ApeContract;
  };

  const mint = async () => {
    if (mintValue < 1) {
      alert("Mint Amount should be at least 1");
      return;
    }
    // Get Ethereum Contract
    const nftContract = await getApeContract();
    const signerAddress = await getSignerAddress();
    const nftContractWithSigner = nftContract.connect(getSigner());

    // Check if Signer is Owner
    const owner = await nftContract.owner();
    // Get Cost
    const cost = await nftContract.cost();
    const Single_Mint_Cost = cost.toString();
    const Total_Cost = Single_Mint_Cost * mintValue;
    let tx;

    try {
      if (owner === signerAddress) {
        tx = await nftContractWithSigner.mint(signerAddress, mintValue);
      } else {
        tx = await nftContractWithSigner.mint(signerAddress, mintValue, {
          value: Total_Cost.toString(),
        });
      }
      await tx.wait();
      // Loader False
      // setIsLoading(false);
    } catch (error) {
      // setIsLoading(false);
      // alert(error.data.message);
      console.log(error.code);
      if (error.code === "INSUFFICIENT_FUNDS") {
        alert("Insufficient Funds!");
      } else if (error.code === 4001) {
        alert("Transaction denied");
      }
      console.log(error);
    }
  };

  useEffect(() => {
    WebFont.load({
      google: {
        families: ["Thirsty Script", "Chilanka"],
      },
    });
    listenMMAccount(); // Event is registered in background
  }, []);

  const styleheading = {
    // Adding media query..
    "@media (max-width: 800px)": {
      fontSize: "2em",
      textAlign: "center",
    },

    "@media (max-width: 600px)": {
      fontSize: "1em",
      textAlign: "center",
    },
  };

  const stylepara = {
    // Adding media query..
    "@media (max-width: 800px)": {
      fontSize: "0.7em",
      textAlign: "center",
    },

    "@media (max-width: 600px)": {
      fontSize: "0.7em",
      textAlign: "center",
    },
  };

  const stylebtn = {
    // Adding media query..
    "@media (max-width: 800px)": {
      width: "20%",
      height: "50%",
      fontSize: "10em",
    },

    "@media (max-width: 600px)": {
      width: "20%",
      height: "50%",
      fontSize: "0.5em",
    },
  };

  const mainimg = {
    // Adding media query..
    "@media (max-width: 2000px)": {
      backgroundImage: ` url(${banner}) `,
      backgroundSize: "cover",
      height: "35em",
    },

    "@media (max-width: 600px)": {
      backgroundImage: ` url(${banner}) `,
      backgroundSize: "cover",
      height: "100%",
    },
  };

  const fontset = {
    // Adding media query..
    "@media (max-width: 2000px)": {
      fontSize: "1.1em",
    },

    "@media (max-width: 800px)": {
      fontSize: "0.7em",
      margin: "0%",
    },
  };

  return (
    <div id="Mint">
      <StyleRoot>
        <div
          style={{
            backgroundColor: "black",
            backgroundSize: "cover",
            margin: "0%",
          }}
        >
          <Container>
            <CardGroup style={{ width: "100%" }}>
              <Card
                style={{
                  backgroundColor: "transparent",
                  margin: "0%",
                  border: "none",
                }}
              >
                {}
              </Card>

              <Card
                style={{
                  backgroundColor: "transparent",
                  margin: "0%",
                  border: "none",
                }}
              >
                <Card.Body
                  style={{
                    width: "100%",
                    marginRight: "auto",
                    marginLeft: "auto",
                  }}
                >
                  <Card.Text
                    style={{
                      fontFamily: "Montserrat-SemiBold",
                      color: "white",
                      fontSize: "0.9em",
                      textAlign: "center",
                    }}
                  >
                    <h1 style={styleheading}>
                      <span style={{ fontFamily: "Inter", color: "white" }}>
                        Welcome to GameWorlds
                      </span>
                    </h1>

                    <br></br>
                    <h5 style={stylepara}>
                      <span
                        style={{
                          fontFamily: "Montserrat-SemiBold",
                          color: "white",
                        }}
                      >
                         MASTER PASS MINTING SOON! Our first NFT will serve as the Master Pass/Founders Pass for GameWorlds. This will grant holders whitelist access to upcoming NFT drops including Wearables, Weapons, Cars, Avatars Powers, Air Drops, and many other accessories and utilities. This will also serve as real-world and in-game event entry. We will also grant beta access to the MasterPass Holders before we launch our Metaverse...... THIS IS THE HIGHEST TIER MEMBERSHIP FOR GAMEWORLDS!!!

                      </span>
                    </h5>

                    <div
                      style={{
                        display: "flex",
                        backgroundColor: "#787878",
                        borderRadius: "20px",
                        marginLeft: "10%",
                        marginRight: "10%",
                        marginTop: "2%",
                      }}
                    >
                      <Button
                        style={{
                          backgroundColor: "transparent",
                          border: "none",
                          margin: "1%",
                          fontFamily: "Montserrat-black",
                        }}
                        onClick={decrementHandler}
                      >
                        <span style={fontset}>-</span>
                      </Button>
                      <span
                        style={{
                          backgroundColor: "transparent",
                          border: "none",
                          margin: "1%",
                          fontFamily: "Montserrat-black",
                          color: "white",
                          fontWeight: "bold",
                          fontSize: "1.5em",
                        }}
                      >
                        <span style={fontset}>{mintValue}</span>
                      </span>
                      <Button
                        style={{
                          backgroundColor: "transparent",
                          border: "none",
                          margin: "1%",
                          fontFamily: "Montserrat-black",
                        }}
                        onClick={incrementHandler}
                      >
                        <span style={fontset}>+</span>
                      </Button>
                      <Button
                        style={{
                          backgroundColor: "transparent",
                          border: "1px solid white",
                          margin: "1%",
                          fontFamily: "Montserrat-black",
                          position: "absolute",
                          right: "12%",
                          fontSize: "1.2em",
                          border: "none",
                          letterSpacing: "2px",
                        }}
                      >
                        <span style={fontset} onClick={maxHandler}>
                          SET MAX
                        </span>
                      </Button>
                    </div>

                    <br></br>
                    <Button
                      variant="warning"
                      style={{
                        border: "1px solid white",
                        margin: "1%",
                        fontFamily: "Montserrat-black",
                        position: "absolute",
                        right: "30%",
                        fontSize: "1.2em",
                        border: "none",
                        letterSpacing: "2px",
                      }}
                    >
                      
                      <span style={fontset}  >
                        
                      </span>
                    </Button>

                    <br></br>
                    <br></br>

                    <h2 style={styleheading}>
                      <br></br>
                      <span
                        style={{
                          fontFamily: "Montserrat-black",
                          paddingTop: "30%",
                        }}
                      >
                        Mint Price is <span>0.13</span>ETH
                        <iframe width="" height="169"  src="https://w2.countingdownto.com/4333437" frameborder="0"></iframe>
                      </span>
                    </h2>
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card
                style={{
                  backgroundColor: "transparent",
                  margin: "0%",
                  border: "none",
                }}
              ></Card>
            </CardGroup>
            
          </Container>
          

        </div>
    
        <img
            src={masterpassg}
            alt="masterpassg.gif"
            style={{
              width: "100%",
              border: "none !important",
              border: "none !important",
              alignItems: "center",
              marginRight: "auto",
              marginLeft: "auto",
            }} 
          />
          
      </StyleRoot>
    </div>
    
  );
};

export default Mine;
